import Vue from "vue";
import VueRouter from "vue-router";

const home = () => import("views/Home");
const welcome = () => import("views/Welcome");
const PersonCard = () => import("views/PersonCard");

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    redirect: "/home",
  },
  {
    path: "/home",
    component: home,
  },
  {
    path: "/welcome",
    component: welcome,
  },
  {
    path: "/PersonCard",
    component: PersonCard,
  },
];

const router = new VueRouter({
  // base: '/wgg/',
  routes,
  mode: "history",
});

export default router;
